@import './mixin.scss';


#Intro {
  background-size: cover; background-position: right bottom; background-repeat: no-repeat;
  display: flex; align-items: center;
  height: 100vh; color: white;
  text-shadow: 2px 2px 2px black; 

  #wrap-text { 
    text-align: center; width: 100%;
    h1 { font-size: 5rem;}
    p { font-size: 1.2rem;}
  }

}